@media (min-width: 0) {
  .harlf-circle {
    width: 100%;
    height: 40vh;
    border-bottom-left-radius: 80vw;
    border-bottom-right-radius: 80vw;
  }

  .half-circle-content {
    h1 {
      font-size: 35px;
      margin-top: 35px;
    }

    p {
      font-size: 15px;
    }
  }
}
@media (min-width: 576px) {
  .half-circle-content {
    h1 {
      font-size: 40px;
    }

    p {
      font-size: 20px;
    }
  }
}

@media (min-width: 768px) {
  .harlf-circle {
    width: 60vw;
    height: 50vh;
    margin-left: 20vw;
    border-bottom-left-radius: 80vh;
    border-bottom-right-radius: 80vh;

    h1 {
      font-size: 45px;
    }

    p {
      font-size: 25px;
    }
  }
}

.all-country-list div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 2vh;
  margin-top: 2vh;
  margin-left: 1vw;
  //  padding-left: 1vw;
  // margin: 2vh 2vw;
}
.all-country-list {
  padding-top: 30vh;
  // justify-content: space-between;
}

.harlf-circle {
  background-color: #89d0ca;
  position: absolute;
  z-index: -1;

  h1 {
    font-family: "Archivo Black";
  }

  p {
    font-family: "Open Sans", sans-serif;
  }
}

.half-circle-content {
  //margin-left: 2vw;
  text-align: center;
  color: #ffffff;
  margin-left: auto;
  margin-right: auto;
}
