@import url("https://fonts.googleapis.com/css2?family=Oswald&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

$break-points: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

.contact-us {
  background-color: #89d0ca;
}

@media (min-width: map-get($break-points, xs)) {
  .contact-us {
    flex-direction: column-reverse;
  }

  .contact-us-img img {
    width: 80vw;
    margin: 5vh auto 5vh auto;
    display: block;
  }
  .contact-us-form {
    margin: 5vh auto 0vh auto;
    width: 80vw;
    h3 {
      font-size: 36px;
      font-family: "Roboto", sans-serif;
      font-weight: 700;
    }
  }

  input {
    margin-top: 5vh;
    width: 60vw;
    font-size: 14px;
    margin-left: auto;
    margin-right: auto;
  }

  input,
  textarea {
    padding: 10px 12px;
  }

  textarea {
    margin-top: 10vh;
    font-size: 14px;
    margin-left: auto;
    margin-right: auto;
    width: 60vw;
  }

  button {
    font-size: 16px;
    padding: 10px 30px;
  }
}

@media (min-width: map-get($break-points, md)) {
  .contact-us {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 120vh;
  }

  .contact-us-img img {
    width: 40vw;
    height: 90vh;
  }

  .contact-us-img {
    margin-left: 5vw;
    margin-top: 20vh;
    position: absolute;
  }
  .contact-us-form {
    width: 40vw;
    height: 80vh;
    margin-top: 10vh;
    padding-left: 10vw;
    margin-left: 38vw;
    padding-right: 5vw;
  }

  input {
    background-color: #ffffff;
    font-size: 14px;
    margin-top: 5vh;
  }

  input {
    margin-top: 5vh;
    padding: 10px 12px;
    width: 40vw;
    padding: 10px 12px;
    font-size: 16px;
  }
  textarea {
    margin-top: 5vh;
    width: 40vw;
    font-size: 16px;
    vertical-align: text-top;
    height: 800px;
  }
}

.contact-us {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

.contact-us-form {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;

  input:focus,
  textarea:focus {
    outline: none;
  }

  h3,
  input,
  textarea {
    border-width: 0 0 2px;
    border-color: black;
    font-family: "Roboto", sans-serif;
  }
  button {
    margin: 5vh auto;
    border-color: black;
    background-color: #ffffff;
    font-family: Oswald, sans-serif;
    font-weight: bold;
  }
  h3 {
    text-align: center;
  }
}
