@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap");

@media (min-width: 0px) {
  .image-covid-vaccine > img {
    width: 60vw;
    height: 60vw;
    border-radius: 50%;
    margin-top: -70vw;
    margin-left: 20vw;
    margin-right: auto;
    position: absolute;
  }

  .shape-circle {
    background-color: #89d0ca;
    width: 60vw;
    height: 60vw;
    border-radius: 50%;
    margin-top: 10vh;
    margin-left: 30vw;
  }
  .wave-image > img {
    width: 40vw;
    margin-left: 2vw;
  }
}

.content-layout {
  display: flex;
  flex-direction: column;
}
@media (min-width: 576px) {
  .image-covid-vaccine > img {
    width: 350px;
    height: 350px;
    border-radius: 50%;
    margin-top: -370px;
    position: absolute;
    margin-left: 20vw;
    margin-right: auto;
  }

  .shape-circle {
    background-color: #89d0ca;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    margin-top: 20vh;
    margin-left: 40vw;
  }

  .text-content {
    margin-top: 10vh;
  }
  .wave-image > img {
    width: 200px;
    margin-left: 4vw;
  }

  .image-content {
    //  margin-left: auto;
    //   margin-right: auto;
  }
}

@media (min-width: 768px) {
  .wave-image {
    img {
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      margin-top: 10vh;
    }
  }

  .shape-circle {
    background-color: #89d0ca;
    height: 400px;
    width: 400px;
    border-radius: 50%;
    margin-top: 20vh;
    margin-left: 8vw;
  }

  .image-covid-vaccine > img {
    border-radius: 50%;
    width: 400px;
    height: 400px;
    margin-top: -50vh;
    margin-left: 5vh;
  }

  .image-covid-vaccine {
    margin-top: -20vh;
  }

  .content-layout {
    display: flex;
    flex-direction: row;
    height: 100%;
  }

  .text-content {
    margin-left: 4vw;
  }

  .image-content {
    width: 80vw;
    margin-left: 4vw;
  }

  .text-content {
    margin-right: 8vw;
    margin-top: 10vh;
    h3 {
      font-size: 1.25em;
    }
    h2 {
      font-size: 2em;
      font-weight: 400;
      span {
        font-weight: 600;
      }
      margin-bottom: 4vh;
    }
    p {
      font-weight: 600;
      margin-bottom: 4vh;
    }
    a {
      font-size: 1.5em;
      background-color: #292e33;
      color: #ffffff;
    }
  }
}
.text-content {
  h3 {
    color: #4e6765;
    font-family: Raleway, sans-serif;
  }

  p {
    margin-top: 0;
    font-family: Raleway, sans-serif;
    color: #4e6765;
    font-weight: 600;
  }
  a {
    text-decoration: none;
    border: 1px solid rgb(15, 15, 15);
    background-color: #292e33;
    color: #ffffff;
  }
}
